@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;0,700;1,300&display=swap);
:root {
  --font-body: 'Lato', sans-serif;
  --font-headings: 'Lato', sans-serif;
  --background-color: #fff;
  --text-color: #fff;
}

.row {
  width: 1140px;
  margin: 0 auto;
}
header {
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url(/travel-demo-front/static/media/hero-1.4e6c5cb2.jpg);
  background-size: cover;
  background-position: center;
  height: 100vh;
  background-attachment: fixed;
  display: flex;
  flex-direction: column;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  background-color: #fff;
  background-color: var(--background-color);
  color: var (#fff);
  color: var (var(--text-color));
  font-family: 'Lato', sans-serif;
  font-family: var(--font-body);
  font-weight: 300;
  font-size: 20px;
  text-rendering: optimizeLegibility;
  overflow-x: hidden;
}

#root {
  display: flex;
  flex-direction: column;
}

h1 {
  font-size: 300%;
  font-weight: 300;
  text-align: center;
}

h2 {
  font-size: 200%;
  font-weight: 300;
}

h3 {
  font-size: 150%;
  font-weight: 400;
}

/* media queries here */

@media only screen and (max-width: 1200px) {
  .row {
    width: 1023px;
  }
}

/* ipads and other */
@media only screen and (max-width: 1023px) {
  .row {
    width: 765px;
  }
}
/*  481-767 */

nav {
  height: 80px;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  font-weight: 400;
}

.spacer {
  flex: 1 1;
}
.nav-items {
  display: flex;
  list-style: none;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.nav-item {
  cursor: pointer;
  display: inline-block;
  padding: 8px 12px;
  text-transform: uppercase;
  font-size: 80%;
  border-bottom: 3px rgba(0, 0, 0, 0) solid;
  transition: border-bottom 0.7s, color 0.5s;
}

.nav-item:hover,
.nav-item:active {
  border-bottom: 3px #51bc9e solid;
  color: #51bc9e;
}

.nav-brand {
  display: flex;
  align-items: center;
}

/*  media queries */

[data-toggle='menu-toggler'] {
  display: none;
}

@media only screen and (max-width: 767px) {
  [data-toggle='menu-toggler'] {
    display: block;
  }
  .row {
    width: 95vw;
  }
  .nav-items {
    display: none;
  }
}

/* 0 -480  */
@media only screen and (max-width: 480px) {
}

/* .svg-icon:hover {
  background-color: #292;
} */

.res-menu {
  display: none;
}

.menu-sidebar.closed {
  display: none;
}
.menu-sidebar {
  width: 100%;
  position: relative;
  background-color: rgba(0, 0, 0, 0.7);
}
.menu-sidebar ul {
  list-style: none;
  align-self: flex-end;
  display: flex;
  flex-direction: column;
}
.menu-sidebar ul li {
  text-align: end;
  padding-right: 10px;
}
.menu-sidebar ul li:hover {
  border-right: 5px solid #51bc9e;
  color: #51bc9e;
}

@media only screen and (max-width: 767px) {
  .res-menu {
    width: 100%;
    z-index: 999;
    display: flex;
    position: fixed;
    top: 0;
    box-sizing: content-box;
    flex-direction: column;
  }
  .res-menu span {
    height: 80px;
    align-self: flex-end;
    top: 40px;
    display: flex;
    align-items: center;
  }
}

.res-menu svg {
  margin-right: 10px;
  background-color: #51bc9e;
  border-radius: 4px;
  box-shadow: 0px 0px 5px 2px rgba(81, 188, 158, 1);
  transition: background-color 1s, box-shadow 1s, font-weight 1s, color 1s;
}

.herobox-container {
  position: relative;
  height: 85vh;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  flex-direction: column;
}

.btn {
  padding: 10px 30px;
  line-height: 25px;
  font-size: 20px;
  cursor: pointer;
  font-weight: 400;
  border-radius: 6px;
}

.btn-hero {
  margin-top: 50px;
  background-color: #51bc9e;
  box-shadow: 0px 0px 5px 0px rgba(237, 220, 237, 1);
  transition: background-color 0.3s ease-in-out, box-shadow 1s;
}
.btn-hero:hover {
  color: #eff6ff;
  background-color: #3d9d82;
  box-shadow: 0px 0px 10px 2px rgba(237, 220, 237, 1);
}

.section-features {
  margin-top: 60px;
  display: flex;
  flex-direction: column;
}

.features-heading h2 {
  width: 100%;
  align-self: center;
  text-align: center;
}
.features-cards {
  margin-top: 20px;
  width: 100%;
  display: flex;
  justify-items: stretch;
  justify-content: space-between;
}
.features-card-item {
  width: 23%;
  min-height: 100%;
  align-content: stretch;
  display: flex;
  flex-direction: column;
}

.card-vector {
  justify-self: center;
  text-align: center;
}

.features-card-body {
  display: flex;
  flex-direction: column;
}
.features-card-body-paragraph {
  text-align: justify;
}
@media only screen and (max-width: 767px) {
  .features-cards {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
  }
  .features-card-item {
    display: block;
    width: 100%;
  }
  .card-vector {
    display: block;
  }
  .card-vector svg {
    margin: 0 auto;
  }
}

.section-favplaces {
  margin-top: 60px;
  display: flex;
  width: 100%;
  flex-direction: column;
}
.fav-places-row {
  width: 100%;
}
.fav-places-row ul {
  width: 100%;
  height: 200px;
  list-style: none;
  display: flex;
  align-content: stretch;
  background-color: #000;
}
.fav-places-row ul li {
  width: 25%;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow: hidden;
}
.fav-places-row ul li img {
  opacity: 0.7;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  display: block;
  transform: scale(1.03);
  transition: transform 1s, opacity 1.5s;
}

.fav-places-row ul li img:hover {
  transform: scale(1.15);
  overflow: hidden;
  opacity: 0.9;
}
@media only screen and (max-width: 767px) {
  .fav-places-row {
    display: none;
  }
}

.section-cities {
  margin-top: 60px;
  display: flex;
  width: 100%;
  flex-direction: column;
}

.cities-container {
  display: flex;
  width: 100%;
  align-content: stretch;
  flex-wrap: wrap;
  justify-content: space-between;
}
@media only screen and (max-width: 767px) {
  .cities-container {
    justify-content: center;
  }
}

.card-container {
  width: 300px;
  display: flex;
  flex-direction: column;
  margin: 15px 20px;
}

.card-image {
  width: 100%;
  overflow: hidden;
}
.card-image img {
  width: 100%;
  background-size: cover;
  background-position: center;
}
.card-body-price {
  text-align-last: right;
}
.card-body h3 {
  padding-left: 0.5rem;
}
.card-body p {
  font-size: 85%;
  text-align: justify;
}

.btn-card {
  cursor: pointer;
  display: block;
  padding: 8px 0px;
  width: 100%;
  text-align: center;
  border-radius: 3px;
  margin: 5px 0;
  font-weight: 400;
}
.success,
.success:focus {
  background-color: #51bc9e;
  color: #ffffff;
  box-shadow: 0px 0px 5px 2px rgba(81, 188, 158, 1);
  transition: background-color 1s, box-shadow 1s, font-weight 1s, color 1s;
}

.success:hover {
  background-color: #3a6e60f8;
  color: #d4ddf4;
  box-shadow: 0px 0px 5px 3px rgba(89, 143, 128, 1);
}
.info,
info:focus {
  background-color: #d4ddf4;
  box-shadow: 0px 0px 5px 2px rgba(186, 197, 224, 1);
  transition: background-color 1s, box-shadow 1s;
}

.info:hover {
  background-color: #788799;
  box-shadow: 0px 0px 5px 3px rgba(108, 133, 161, 1);
}

.section-guest-testimonials {
  margin-top: 60px;
  padding-bottom: 15px;
  display: flex;
  width: 100%;
  background-attachment: fixed;
  background-image: linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)),
    url(/travel-demo-front/static/media/testimonials.00258f12.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  align-content: initial;
}
.testimonials {
  width: 100%;
  display: flex;
  flex-direction: column;
}
.testimonials h2 {
  text-align: center;
  color: #fff;
}
.testimonials-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.testimonial-container {
  display: flex;
  flex-direction: column;
  width: 30%;
  color: #fff;
}
.testimonial-container div {
  padding-top: 10px;
  display: flex;
  align-content: center;
  align-items: center;
}
.testimonial-container img {
  width: 100px;
  height: 100px;
  background-size: cover;
  background-position: center;
  border-radius: 50%;
}

.testimonial-container blockquote {
  position: relative;
  font-style: italic;
  padding-top: 15px;
}
.testimonial-container blockquote::before {
  content: '\201C';
  font-size: 400%;
  display: block;
  position: absolute;
  top: -15px;
  left: -5px;
}
.row-wrapper {
  display: flex;
  justify-content: space-between;
  width: 100%;
  height: 100%;
}

@media only screen and (max-width: 767px) {
  .row-wrapper {
    flex-direction: column;
  }
  .testimonial-container {
    width: 100%;
    margin-bottom: 10px;
  }
}

.section-feedback {
  margin-top: 60px;
}

.feedback {
  margin: 0 auto;
  display: flex;
  padding-top: 20px;
  flex-direction: column;
  width: 500px;
}

.feedback label {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 5px;
}

.feedback input {
  height: 40px;
  width: 300px;
  border: none;
  outline: none;
  padding: 4px 12px;
  border-radius: 4px;
  box-shadow: 0px 0px 5px 0px rgba(55, 179, 169, 1);
  border-color: rgba(55, 179, 169, 1);
  font-size: 100%;
}

.feedback input:focus {
  border-color: rgb(31, 105, 99);
  box-shadow: 0px 0px 6px 2px rgba(55, 179, 169, 1);
}

.feedback textarea {
  width: 300px;
  height: 100px;
  padding: 10px 12px;
  box-shadow: 0px 0px 5px 0px rgba(55, 179, 169, 1);
  resize: none;
  outline: none;
  border: none;
  border-radius: 4px;
}

@media only screen and (max-width: 767px) {
  .feedback label {
    flex-direction: column;
    width: 100%;
    margin: 5px 0px;
    align-self: flex-start;
  }
  .feedback {
    width: 100%;
    margin: 0;
  }
}
@media only screen and (max-width: 310px) {
  .feedback input,
  .feedback textarea {
    width: 90%;
  }
}

footer {
  margin-top: 60px;
  background-color: #20252b;
  height: 200px;
  color: #f2f2f2;
}

.footer-container {
  height: 200px;
  display: flex;
  align-items: center;
}
footer ul {
  list-style: none;
  display: flex;
  height: 100%;
  flex-direction: column;
}
footer ul li {
  cursor: pointer;
  display: flex;
  align-items: center;
}

